  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

/* done with steps */
#getStepsDone{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
