      align-items: center;
  margin-top: 25px;
}




/* screen max width 648px */
@media screen and (max-width: 648px){
  /* first step page */
  #showRegister{
    display: -ms-flexbox;
    display: flex; 
    -ms-flex-align: center; 
        align-items: center; 
    -ms-flex-pack: center; 
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  #emailWachtwoordForm{
    margin: 10px 10px 10px 10px;
  }

  #Waarom {
    margin: 10px 10px 10px 10px;
  }

  #lijn{
    border: 1px solid rgb(231, 231, 231);
    height: 1px;
    max-width:350px;
    width: 350px;
    margin: 15px 15px 15px 15px;
  }

  /* second step page */
  #showNaamAdres{
    display: -ms-flexbox;
    display: flex; 
    -ms-flex-align: center; 
        align-items: center; 
    -ms-flex-pack: center; 
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  #naam{
    width:100%;
    margin: 10px 10px 10px 10px;
    
  }

  #adres{
    width:100%;
    margin: 10px 10px 10px 10px;
  }

  #button{
    width: 100%;
  }

  #buttonsBackNext{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
  }
}




